import React, { useState, useEffect, useContext, useRef } from 'react';
import {
  Box,
  TextField,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Button,
  Typography,
  Paper,
  List,
  ListItem,
  ListItemText,
  IconButton,
  Tooltip,
  Stack,
  useMediaQuery,
  useTheme,
  Drawer,
  Fab,
  Divider,
  Chip,
  Popover,
  ListItemButton,
  ListItemIcon,
  ClickAwayListener,
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';
import RestartAltIcon from '@mui/icons-material/RestartAlt';
import ZoomInIcon from '@mui/icons-material/ZoomIn';
import SearchIcon from '@mui/icons-material/Search';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import HistoryIcon from '@mui/icons-material/History';
import ClearIcon from '@mui/icons-material/Clear';
import DeleteIcon from '@mui/icons-material/Delete';
import bibleData from '../data/bible.json';
import { BibleData, BibleVerse, SearchParams } from '../types/bible';
import { FontSizeContext, ColorModeContext } from '../App';

//   
const SEARCH_HISTORY_KEY = 'bible-search-history';

//    
interface SearchHistory extends SearchParams {
  id: string;
  timestamp: number;
  displayText: string;
}

const bible = bibleData as BibleData;

export const BibleSearch: React.FC = () => {
  const [books, setBooks] = useState<string[]>([]);
  const [chapters, setChapters] = useState<string[]>([]);
  const [searchParams, setSearchParams] = useState<SearchParams>({});
  const [results, setResults] = useState<BibleVerse[]>([]);
  const [showFontControls, setShowFontControls] = useState(false);
  const [searchHistory, setSearchHistory] = useState<SearchHistory[]>([]);
  const [historyAnchorEl, setHistoryAnchorEl] = useState<HTMLButtonElement | null>(null);
  
  const { fontSize, increaseFontSize, decreaseFontSize, resetFontSize } = useContext(FontSizeContext);
  const { mode, toggleColorMode } = useContext(ColorModeContext);
  
  //     
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const isTablet = useMediaQuery(theme.breakpoints.down('md'));
  
  //   
  useEffect(() => {
    try {
      const storedHistory = localStorage.getItem(SEARCH_HISTORY_KEY);
      if (storedHistory) {
        setSearchHistory(JSON.parse(storedHistory));
      }
    } catch (error) {
      console.error('     :', error);
    }
  }, []);

  //   
  const saveSearchHistory = (history: SearchHistory[]) => {
    try {
      localStorage.setItem(SEARCH_HISTORY_KEY, JSON.stringify(history));
      setSearchHistory(history);
    } catch (error) {
      console.error('     :', error);
    }
  };

  useEffect(() => {
    //     
    const allBooks = new Set<string>();
    Object.keys(bible).forEach(reference => {
      const book = reference.split(/\d/)[0]; //     
      allBooks.add(book);
    });
    setBooks(Array.from(allBooks));
  }, []);

  useEffect(() => {
    if (searchParams.book) {
      //     
      const allChapters = new Set<string>();
      Object.keys(bible).forEach(reference => {
        if (reference.startsWith(searchParams.book || '')) {
          const match = reference.match(/\d+/);
          if (match) {
            allChapters.add(match[0]);
          }
        }
      });
      setChapters(Array.from(allChapters));
    } else {
      setChapters([]);
    }
  }, [searchParams.book]);

  //   
  const addToSearchHistory = (params: SearchParams) => {
    //    
    if (!params.keyword && !params.book && !params.chapter) return;
    
    //    
    const displayText = getDisplayText(params);
    
    //     
    const existingIndex = searchHistory.findIndex(
      item => (
        item.keyword === params.keyword &&
        item.book === params.book &&
        item.chapter === params.chapter
      )
    );
    
    const newHistoryItem: SearchHistory = {
      ...params,
      id: Date.now().toString(),
      timestamp: Date.now(),
      displayText,
    };
    
    let updatedHistory: SearchHistory[];
    
    if (existingIndex !== -1) {
      //       
      updatedHistory = [...searchHistory];
      updatedHistory.splice(existingIndex, 1);
      updatedHistory.unshift(newHistoryItem);
    } else {
      //    
      updatedHistory = [newHistoryItem, ...searchHistory];
    }
    
    //  20 
    if (updatedHistory.length > 20) {
      updatedHistory = updatedHistory.slice(0, 20);
    }
    
    saveSearchHistory(updatedHistory);
  };
  
  //    
  const removeFromSearchHistory = (id: string) => {
    const updatedHistory = searchHistory.filter(item => item.id !== id);
    saveSearchHistory(updatedHistory);
  };
  
  //    
  const clearSearchHistory = () => {
    saveSearchHistory([]);
  };
  
  //    
  const getDisplayText = (params: SearchParams): string => {
    const parts = [];
    
    if (params.keyword) {
      parts.push(`"${params.keyword}"`);
    }
    
    if (params.book) {
      if (params.chapter) {
        parts.push(`${params.book} ${params.chapter}`);
      } else {
        parts.push(params.book);
      }
    }
    
    return parts.length > 0 ? parts.join(' - ') : ' ';
  };

  const handleSearch = () => {
    const { keyword, book, chapter } = searchParams;
    const searchResults: BibleVerse[] = [];

    for (const [reference, text] of Object.entries(bible)) {
      //    
      if (book && !reference.startsWith(book)) continue;
      
      //  
      if (chapter) {
        const regex = new RegExp(`${book}${chapter}:`);
        if (!regex.test(reference)) continue;
      }
      
      //  
      if (keyword && !text.includes(keyword)) continue;

      //  
      const bookMatch = reference.split(/\d/)[0];
      const chapterMatch = reference.match(/\d+/);
      const verseMatch = reference.match(/:(\d+)/);
      
      if (bookMatch && chapterMatch && verseMatch) {
        searchResults.push({
          book: bookMatch,
          chapter: chapterMatch[0],
          verse: verseMatch[1],
          text: text,
        });
      }
    }

    setResults(searchResults);
    
    //   
    addToSearchHistory({ ...searchParams });
  };

  const handleKeyDown = (event: React.KeyboardEvent) => {
    if (event.key === 'Enter') {
      handleSearch();
    }
  };

  const toggleFontControls = () => {
    setShowFontControls(!showFontControls);
  };
  
  //    
  const handleHistoryClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setHistoryAnchorEl(event.currentTarget);
  };
  
  //    
  const handleHistoryClose = () => {
    setHistoryAnchorEl(null);
  };
  
  //    
  const handleHistoryItemClick = (historyItem: SearchHistory) => {
    setSearchParams({
      keyword: historyItem.keyword,
      book: historyItem.book,
      chapter: historyItem.chapter,
    });
    handleHistoryClose();
    handleSearch();
  };
  
  const historyOpen = Boolean(historyAnchorEl);

  return (
    <Box sx={{ 
      maxWidth: isMobile ? '100%' : '800px', 
      width: '100%',
      mx: 'auto', 
      p: isMobile ? 2 : 3,
      boxSizing: 'border-box',
    }}>
      <Box sx={{ 
        display: 'flex', 
        flexDirection: isMobile ? 'column' : 'row',
        justifyContent: 'space-between', 
        alignItems: isMobile ? 'flex-start' : 'center', 
        mb: 2,
        position: 'relative',
      }}>
        <Typography 
          variant={isMobile ? "h5" : "h4"} 
          gutterBottom
          sx={{ mb: isMobile ? 2 : 'inherit' }}
        >
           
        </Typography>
        
        <Box sx={{ 
          position: isMobile ? 'absolute' : 'static', 
          top: isMobile ? 0 : 'auto', 
          right: isMobile ? 0 : 'auto',
          display: 'flex',
          alignItems: 'center',
        }}>
          {/*    */}
          <Tooltip title=" ">
            <IconButton 
              onClick={handleHistoryClick}
              color="inherit" 
              size={isMobile ? "small" : "medium"}
              sx={{ mr: 1 }}
            >
              <HistoryIcon />
            </IconButton>
          </Tooltip>
          
          {/*     */}
          <Tooltip title={mode === 'light' ? "  " : "  "}>
            <IconButton 
              onClick={toggleColorMode} 
              color="inherit" 
              size={isMobile ? "small" : "medium"}
              sx={{ mr: 1 }}
            >
              {mode === 'light' ? <Brightness4Icon /> : <Brightness7Icon />}
            </IconButton>
          </Tooltip>
          
          <Tooltip title="  ">
            <IconButton onClick={toggleFontControls} color="primary" size={isMobile ? "small" : "medium"}>
              <ZoomInIcon />
            </IconButton>
          </Tooltip>
          
          {showFontControls && (
            <Stack 
              direction="row" 
              spacing={1} 
              sx={{ 
                display: 'inline-flex', 
                ml: 1,
                p: isMobile ? 1 : 0,
                bgcolor: isMobile ? 'background.paper' : 'transparent',
                boxShadow: isMobile ? 1 : 0,
                borderRadius: 1,
              }}
            >
              <Tooltip title="  ">
                <IconButton 
                  onClick={decreaseFontSize} 
                  size="small"
                  disabled={fontSize <= 12}
                >
                  <RemoveIcon fontSize="small" />
                </IconButton>
              </Tooltip>
              
              <Typography sx={{ display: 'flex', alignItems: 'center' }}>
                {fontSize}px
              </Typography>
              
              <Tooltip title="  ">
                <IconButton 
                  onClick={increaseFontSize} 
                  size="small"
                  disabled={fontSize >= 24}
                >
                  <AddIcon fontSize="small" />
                </IconButton>
              </Tooltip>
              
              <Tooltip title="  ">
                <IconButton 
                  onClick={resetFontSize} 
                  size="small"
                  disabled={fontSize === 16}
                >
                  <RestartAltIcon fontSize="small" />
                </IconButton>
              </Tooltip>
            </Stack>
          )}
        </Box>
      </Box>
      
      <Paper sx={{ p: isMobile ? 2 : 3, mb: 3 }}>
        <Box sx={{ 
          display: 'flex', 
          flexDirection: isMobile ? 'column' : 'row',
          gap: 2, 
          flexWrap: 'wrap'
        }}>
          <TextField
            label=""
            value={searchParams.keyword || ''}
            onChange={(e) => setSearchParams({ ...searchParams, keyword: e.target.value })}
            onKeyDown={handleKeyDown}
            sx={{ 
              width: isMobile ? '100%' : isTablet ? '45%' : 'auto',
              minWidth: !isMobile ? 200 : 'auto',
            }}
            fullWidth={isMobile}
          />
          
          <FormControl sx={{ 
            width: isMobile ? '100%' : isTablet ? '45%' : 'auto',
            minWidth: !isMobile ? 200 : 'auto',
          }}>
            <InputLabel></InputLabel>
            <Select
              value={searchParams.book || ''}
              label=""
              onChange={(e) => setSearchParams({ ...searchParams, book: e.target.value })}
              fullWidth
            >
              {books.map((book) => (
                <MenuItem key={book} value={book}>
                  {book}
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          <FormControl sx={{ 
            width: isMobile ? '100%' : isTablet ? '45%' : 'auto',
            minWidth: !isMobile ? 200 : 'auto',
          }}>
            <InputLabel></InputLabel>
            <Select
              value={searchParams.chapter || ''}
              label=""
              onChange={(e) => setSearchParams({ ...searchParams, chapter: e.target.value })}
              disabled={!searchParams.book}
              fullWidth
            >
              {chapters.map((chapter) => (
                <MenuItem key={chapter} value={chapter}>
                  {chapter}
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          <Button 
            variant="contained" 
            onClick={handleSearch}
            fullWidth={isMobile}
            startIcon={isMobile ? <SearchIcon /> : null}
          >
            
          </Button>
        </Box>
        
        {/*     */}
        {searchHistory.length > 0 && (
          <Box sx={{ 
            display: 'flex', 
            flexWrap: 'wrap', 
            gap: 1, 
            mt: 2,
            alignItems: 'center'
          }}>
            <Typography variant="body2" color="text.secondary" sx={{ mr: 1 }}>
               :
            </Typography>
            {searchHistory.slice(0, 5).map((item) => (
              <Chip
                key={item.id}
                label={item.displayText}
                size="small"
                color="primary"
                variant="outlined"
                onClick={() => handleHistoryItemClick(item)}
                sx={{ 
                  maxWidth: isMobile ? '100%' : 200,
                  fontSize: '0.75rem',
                  '& .MuiChip-label': { 
                    whiteSpace: 'nowrap',
                    overflow: 'hidden',
                    textOverflow: 'ellipsis'
                  } 
                }}
              />
            ))}
          </Box>
        )}
      </Paper>

      <Paper>
        <List sx={{ 
          p: isMobile ? 1 : 2 
        }}>
          {results.length > 0 ? (
            results.map((verse, index) => (
              <ListItem 
                key={index} 
                divider
                sx={{
                  flexDirection: 'column',
                  alignItems: 'flex-start',
                  padding: isMobile ? '12px 0' : '20px 0',
                }}
              >
                <Typography 
                  component="div" 
                  variant={isMobile ? "subtitle1" : "h6"}
                  sx={{ 
                    fontWeight: 'bold',
                    mb: 2
                  }}
                >
                  {`${verse.book} ${verse.chapter} ${verse.verse}`}
                </Typography>
                
                <Typography 
                  component="div" 
                  variant="body1"
                  sx={{ 
                    width: '100%',
                    lineHeight: 1.8,
                    wordBreak: 'keep-all',
                    pl: 1
                  }}
                >
                  {verse.text}
                </Typography>
              </ListItem>
            ))
          ) : (
            <ListItem>
              <ListItemText 
                primary={
                  <Typography 
                    sx={{ 
                      textAlign: 'center',
                      py: 2 
                    }}
                  >
                      .   .
                  </Typography>
                } 
              />
            </ListItem>
          )}
        </List>
      </Paper>
      
      {/*       */}
      {isMobile && results.length > 5 && (
        <Fab 
          color="primary" 
          aria-label="" 
          sx={{ 
            position: 'fixed', 
            bottom: 16, 
            right: 16,
            zIndex: 1000,
          }}
          onClick={() => {
            window.scrollTo({ top: 0, behavior: 'smooth' });
          }}
        >
          <SearchIcon />
        </Fab>
      )}
      
      {/*    */}
      <Popover
        open={historyOpen}
        anchorEl={historyAnchorEl}
        onClose={handleHistoryClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
      >
        <Box
          sx={{ 
            width: isMobile ? 300 : 400, 
            maxHeight: 400,
            overflow: 'auto',
            p: 1 
          }}
        >
          <Box sx={{ 
            display: 'flex', 
            justifyContent: 'space-between', 
            alignItems: 'center',
            p: 1,
            mb: 1,
            borderBottom: 1, 
            borderColor: 'divider' 
          }}>
            <Typography variant="subtitle1" fontWeight="bold">
               
            </Typography>
            {searchHistory.length > 0 && (
              <Tooltip title=" ">
                <IconButton size="small" onClick={clearSearchHistory}>
                  <DeleteIcon fontSize="small" />
                </IconButton>
              </Tooltip>
            )}
          </Box>
          
          {searchHistory.length > 0 ? (
            <List dense>
              {searchHistory.map((item) => (
                <ListItem 
                  key={item.id} 
                  disablePadding
                  secondaryAction={
                    <IconButton 
                      edge="end" 
                      aria-label="" 
                      size="small"
                      onClick={(e) => {
                        e.stopPropagation();
                        removeFromSearchHistory(item.id);
                      }}
                    >
                      <ClearIcon fontSize="small" />
                    </IconButton>
                  }
                >
                  <ListItemButton 
                    onClick={() => handleHistoryItemClick(item)}
                    dense
                  >
                    <ListItemIcon sx={{ minWidth: 36 }}>
                      <HistoryIcon fontSize="small" />
                    </ListItemIcon>
                    <ListItemText 
                      primary={item.displayText}
                      primaryTypographyProps={{ 
                        noWrap: true,
                        sx: { maxWidth: isMobile ? 180 : 280 } 
                      }}
                      secondary={new Date(item.timestamp).toLocaleString()}
                      secondaryTypographyProps={{
                        variant: 'caption',
                        fontSize: '0.7rem'
                      }}
                    />
                  </ListItemButton>
                </ListItem>
              ))}
            </List>
          ) : (
            <Box sx={{ py: 2, textAlign: 'center' }}>
              <Typography variant="body2" color="text.secondary">
                  .
              </Typography>
            </Box>
          )}
        </Box>
      </Popover>
    </Box>
  );
}; 