import React, { useState, createContext, useCallback } from 'react';
import { CssBaseline, ThemeProvider, createTheme, PaletteMode } from '@mui/material';
import { BibleSearch } from './components/BibleSearch';

//    
export const FontSizeContext = createContext({
  fontSize: 16,
  increaseFontSize: () => {},
  decreaseFontSize: () => {},
  resetFontSize: () => {},
});

//    
export const ColorModeContext = createContext({
  mode: 'light' as PaletteMode,
  toggleColorMode: () => {},
});

function App() {
  const [fontSize, setFontSize] = useState(16); //    16px
  const [mode, setMode] = useState<PaletteMode>('light'); //   

  //    
  const increaseFontSize = useCallback(() => {
    setFontSize(prevSize => Math.min(prevSize + 1, 24));
  }, []);

  //    
  const decreaseFontSize = useCallback(() => {
    setFontSize(prevSize => Math.max(prevSize - 1, 12));
  }, []);

  //    
  const resetFontSize = useCallback(() => {
    setFontSize(16);
  }, []);

  //    
  const toggleColorMode = useCallback(() => {
    setMode(prevMode => prevMode === 'light' ? 'dark' : 'light');
  }, []);

  //        
  const theme = createTheme({
    palette: {
      mode,
      primary: {
        main: mode === 'light' ? '#1976d2' : '#90caf9',
      },
      secondary: {
        main: mode === 'light' ? '#dc004e' : '#f48fb1',
      },
      background: {
        default: mode === 'light' ? '#fff' : '#121212',
        paper: mode === 'light' ? '#fff' : '#1e1e1e',
      },
      text: {
        primary: mode === 'light' ? 'rgba(0, 0, 0, 0.87)' : 'rgba(255, 255, 255, 0.87)',
        secondary: mode === 'light' ? 'rgba(0, 0, 0, 0.6)' : 'rgba(255, 255, 255, 0.6)',
      },
    },
    typography: {
      fontSize: fontSize,
    },
  });

  return (
    <ColorModeContext.Provider
      value={{
        mode,
        toggleColorMode
      }}
    >
      <FontSizeContext.Provider 
        value={{ 
          fontSize, 
          increaseFontSize, 
          decreaseFontSize, 
          resetFontSize 
        }}
      >
        <ThemeProvider theme={theme}>
          <CssBaseline />
          <BibleSearch />
        </ThemeProvider>
      </FontSizeContext.Provider>
    </ColorModeContext.Provider>
  );
}

export default App; 